.destinationModel {
	grid-template-rows: 12rem 5rem 13rem auto;
	--img-url: url("../../../assets/destination/background-destination-mobile.jpg");
	background-image: var(--img-url);
	padding: 8rem 1rem 2rem;
	min-height: 100vh;
}

.activeLink {
	border-bottom: 2px solid white;
	padding-bottom: 0.6rem;
}

.pic {
	width: 11rem;
	height: 9rem;
}

.destinationModel hr {
	height: 1px;
	width: 86%;
	margin: 0 auto;
	border: none;
	opacity: 0.3;
}

.destinationModel footer {
	flex-direction: column;
}

@media (min-width: 560px) {
	.destinationModel {
		--img-url: url("../../../assets/destination/background-destination-tablet.jpg");
		padding: 10rem 4rem 2rem;
		grid-template-rows: 10rem 5rem 13rem;
	}

	.pic {
		width: 14rem;
		height: 11rem;
	}

	.destinationModel header {
		text-align: left;
	}

	.destinationModel footer {
		--gap: 4rem;
		margin: 0 auto;
		flex-direction: row;
	}
}

@media (min-width: 769px) {
	.destinationModel {
		--img-url: url("../../../assets/destination/background-destination-desktop.jpg");
		grid-template-columns: 46% 2rem 44%;
		padding: 12rem 1rem 4rem;
		grid-template-rows: 6rem 12rem 3rem 6rem;
		grid-template-areas:
			"head . list"
			"head . main"
			"head . hr"
			"head . footer";
		text-align: left;
	}

	.pic {
		width: 20rem;
		height: 18rem;
	}

	.destinationModel header {
		grid-area: head;
		gap: 4rem;
	}

	.destinationModel header h1 {
		padding: 0 0 2rem 4rem;
	}
	.destinationModel ul {
		grid-area: list;
		margin: 0;
		padding: 0;
	}
	.destinationModel main {
		grid-area: main;
		margin: 0;
		padding: 0;
	}
	.destinationModel main h1 {
		font-size: 5rem;
	}
	.destinationModel main p {
		font-size: 1.08rem;
	}

	.destinationModel hr {
		grid-area: hr;
		margin: 0;
	}
	.destinationModel footer {
		grid-area: footer;
		margin: 0;
		padding: 0;
	}

	.destinationModel footer h3 {
		font-size: 1.06rem;
	}
	.destinationModel footer span {
		font-size: 1.4rem;
	}
}

@media (min-width: 1200px) {
	.destinationModel {
		padding: 14rem 8rem 4rem;
		grid-template-rows: 6rem 12rem 3rem 6rem;
		font-size: 2rem;
		grid-template-columns: 46% 8% 44%;
	}

	.destinationModel > * {
		font-size: 2rem;
	}

	.pic {
		width: 32rem;
		height: 28rem;
	}
}
